import React from "react";
import "./css/Main.css";
import "./css/About.css";
import sky from "./img/background_sky.mp4";
function About() {
  return (
    <div id="about">
      <div id="standard">
        <video
          className="video"
          src={sky}
          autoplay="autoplay"
          loop="loop"
          muted
        ></video>
        <div className="profile">
          <p>
            Jiho's <br />
            Portfolio
          </p>
        </div>
      </div>
    </div>
  );
}

export default About;
